<template>
    <div class="col-12">
        <h3>{{ title }}</h3>
        <div class="justify-content-center row">
            <!-- <b-form-fieldset horizontal label="Rows per page" class="col-6" :label-size="6">
                <b-form-select :options="[{text:5,value:5},{text:10,value:10},{text:15,value:15}]" v-model="perPage">
                </b-form-select>
            </b-form-fieldset> -->
            <b-form-fieldset horizontal :label="actionsLabel" class="col-6">
                <b-form-select v-show="actionsLabel" :options="actions">
                </b-form-select>
            </b-form-fieldset>

            <b-form-fieldset horizontal class="col-2">
                <b-button variant="primary" href="" v-if="crudActions && fields.modelActions.create" v-on:click="createRegistry()">{{ $t("grid.add") }}</b-button>
            </b-form-fieldset>

            <b-form-fieldset horizontal :label="$t('grid.filter')" class="col-4" :label-cols="2">
                <b-form-input v-model="filter" :placeholder="$t('grid.typetosearch')"></b-form-input>
            </b-form-fieldset>
        </div>

        <!-- Main table element -->
        <b-table striped hover :items="items" :fields="fields" :current-page="currentPage" :per-page="perPage" :filter="filter" v-on:row-clicked="rowClicked($event)">
            <!-- <template slot="name" scope="item">
                {{item.value.first}} {{item.value.last}}
            </template>
            <template slot="isActive" scope="item">
                {{item.value?'Yes :)':'No :('}}
            </template> -->
            <template slot="modelActions" scope="item">
                <a href="#" v-if="crudActions && fields.modelActions.edit" @click.stop="editRegistry(item)"><icon name="edit"></icon></a>
                <a href="#" v-if="crudActions && fields.modelActions.delete" @click.stop="deleteRegistry(item);$root.$emit('show::modal','deleteModal')"><icon name="trash"></icon></a>
            </template>
        </b-table>

        <div class="justify-content-between row">
          <div class="col-2">
            <b-form-fieldset horizontal :label="$t('grid.rowsperpage')" label-cols="8">
                <b-form-select :options="[{text:5,value:5},{text:10,value:10},{text:15,value:15}]" v-model="perPage">
                </b-form-select>
            </b-form-fieldset>
          </div>
          <b-form-fieldset horizontal class="col-3">
              <b-pagination size="md" :total-rows="this.items.length" :per-page="perPage" v-model="currentPage" />
          </b-form-fieldset>
        </div>

        <b-modal id="deleteModal" title="Inter-IoT" @ok="deleteRegistry()">
            <p>{{ deleteMessage }}</p>
        </b-modal>
    </div>
</template>

<script>
import * as Icon from 'vue-awesome';
import { bus } from '../main';

export default {
    props: [
        'title',
        'items',
        'fields',
        'actions',
        'crudActions',
        'preventRowClickEdit'
    ],
    components: {
        'icon': Icon
    },
    data() {
        return {
            actionsLabel: 'Actions',
            deleteTitle: 'Delete',
            deleteMessage: 'Registry will be removed. Are you sure?',
            deleteIndex: null,
            deleteClicked: false,
            currentPage: 1,
            perPage: 10,
            filter: null
        }
    },
    created: function() {
        if (this.actions.length == 0) {
            this.actionsLabel = '';
        }
        bus.$on('language', (data) => {
            this.$i18n.locale = data.code;
        });
    },
    methods: {
        createRegistry: function() {
            this.$emit('createRegistry');
        },
        rowClicked: function(item){
          if(this.preventRowClickEdit) this.$emit('rowClicked', item);
          else this.editRegistry(item);
        },
        editRegistry: function(item) {
            if (this.deleteClicked) {
                this.deleteClicked = false;
                return;
            }
            if (item.item) {
                this.$emit('editRegistry', item.item);
            } else {
                this.$emit('editRegistry', item);
            }
        },
        deleteRegistry: function(item) {
            if (item) {
                this.deleteIndex = item.item._id || item.item.id;
                this.deleteClicked = true;
            } else {
                if (this.deleteIndex) {
                    this.$emit('deleteRegistry', this.deleteIndex);
                }
            }
        }
    }
}
</script>

<i18n>
{
    "en": {
        "grid.add": "Add",
        "grid.filter": "Filter",
        "grid.typetosearch": "Type to Search",
        "grid.rowsperpage": "Rows per page"
    },
    "es": {
        "grid.add": "Nuevo",
        "grid.filter": "Filtro",
        "grid.typetosearch": "Buscar",
        "grid.rowsperpage": "Filas por pgina"
    },
    "ca": {
        "grid.add": "Nou",
        "grid.filter": "Filtre",
        "grid.typetosearch": "Buscar",
        "grid.rowsperpage": "Files per pgina"
    }
}
</i18n>



