<template>
  <component v-bind:is="item" v-on:openGwComponent="openGwComponent" v-bind:data="data"></component>
</template>

<script>
import { bus } from '../../main';
import GatewayGrid from './gatewayGrid.vue';
import GatewayTabPane from './gatewayTabPane.vue';
import Api from './api';

export default {
  components: {
    'gateway-grid': GatewayTabPane,
    'gateway-tabpane': GatewayTabPane
  },
  data: function() {
    return {
      item: null,
      data: {}
    }
  },
  mounted: function(){
    const gateway = {};
    gateway.url = `${window.location.protocol}//${window.location.hostname}`;
    gateway.port = window.location.port === "" ? window.location.protocol === 'https:' ? 443: 80 : window.location.port;
    gateway.api = new Api(this.$http, `${gateway.url}:${gateway.port}`);
    gateway.api.connect();
    gateway.api.on(gateway.api.events.VIRTUALCONNECTED, (err, version) => {
      const item = {};
      item.online = !err;
      item.version = err?'-':version;
      console.log(item);
      this.openGwComponent('gateway-grid', {gateway: gateway});
    });
    gateway.api.on(gateway.api.events.VIRTUALDISCONNECTED, (err) => {
      const item = {};
      item.online = false;
      item.version = '-';
      console.log(item);
      gateway.api.connect();
    });
    
  },
  methods: {
    openGwComponent: function(component, data) {
      this.data = data;
      this.item = component;
    }
  }
}
</script>


