<template>
  <div class="row">
    <div class="col-12">
      <div class="row">
        <app-grid v-bind:title="title" v-bind:items="items" v-bind:fields="fields"
          v-bind:actions="actions" v-bind:crudActions="crudActions" v-on:createRegistry="createRegistry()" 
          v-on:editRegistry="editRegistry($event)" v-on:deleteRegistry="deleteRegistry($event)"
          v-on:rowClicked="rowClicked($event)" preventRowClickEdit=true></app-grid>
      </div>
      <device-modal ref="deviceModal" v-bind:data="data" />
    </div>
  </div>
</template>

<script>
import Grid from '../../../../components/grid.vue';
import DeviceModal from './deviceModal.vue';

const deviceStatusFormatter = (msg) => {
  let color = 'gray';
  msg = msg || 'ERROR';
  switch(msg){
    case 'DISCONNECTED':
      color = 'gray';
      break;
    case 'READY':
      color = 'blue';
      break;
    case 'CONNECTING':
      color = 'green';
      break;
    case 'CONNECTED':
      color = 'green';
      break;
    case 'FAILURE':
      color = 'red';
      break;
    default:
      color = 'red';
  }
  return `<span style="color: ${color};">${msg}</span>`;
}

export default {
  components: {
    'app-grid': Grid,
    'device-modal': DeviceModal
  },
  props: [
    'data'
  ],
  data() {
    return {
      crudActions: this.$store.getters.hasFeature('edit_gateways'),
      items: [],
      title: 'Devices',
      fields: {
        uuid: {
          label: 'UUID',
          sortable: true
        },
        name: {
          label: 'Name',
          sortable: true
        },
        controller: {
          label: 'Controller',
          sortable: true
        },
        sensors: {
          label: 'Sensors',
          sortable: true
        },
        actuators: {
          label: 'Actuators',
          sortable: true,
        },
        status: {
          label: 'Status',
          sortable: true,
          formatter: deviceStatusFormatter
        },
        modelActions: {
          label: 'Actions',
          create: true,
          edit: true,
          delete: true
        }
      },
      actions: [],
      listener: null
    }
  },
  created: function() {
    this.listener = this.data.gateway.api.listen('getDevices', (err, devices) => {
      this.refreshData(devices);
    });
  },
  beforeDestroy: function() {
    this.data.gateway.api.clear(this.listener);
    this.items.forEach(item => this.data.gateway.api.clear(item.listener));
  },
  methods: {
    refreshData: function(devices) {
      const newUuids = devices.map(dev => dev.uuid);
      const oldUuids = this.items.map(item => item.uuid);
      const toAdd = newUuids.filter(uuid => oldUuids.indexOf(uuid) === -1).map(uuid => devices.find(dev => dev.uuid === uuid));
      const toRemove = oldUuids.filter(uuid => newUuids.indexOf(uuid) === -1).map(uuid => this.items.findIndex(item => item.uuid === uuid)).sort((a, b) => b - a);
      toRemove.forEach(idx => {
        this.data.gateway.api.clear(this.items[idx].listener);
        this.items.splice(idx, 1);
      });
      toAdd.map(device => {
        const item = device;
        item.sensors = 0;
        item.actuators = 0;
        for(let io in device.deviceIOs){
          switch(device.deviceIOs[io].type){
            case 'SENSOR': item.sensors++; break;
            case 'ACTUATOR': item.actuators++; break;
          }
        }
        item.status = null;
        item.listener = this.data.gateway.api.listen('deviceStatus', item.uuid, (err, status) => {
          item.status = status;
        });
        return item;
      }).forEach(item => this.items.push(item));
      return;
    },
    createRegistry: function() {
        /*this.selected = {
            name: '',
            type: '',
            url: '',
            port: '',
            owner: '',
            icon: 'sitemap'
        };*/
        //this.$refs.myModalRef.show();
    },
    rowClicked: function(item){
      this.$refs.deviceModal.open(item);
    },
    editRegistry: function(item) {
      //this.selected = item;
      //this.$refs.myModalRef.show();
    },
    deleteRegistry: function(itemId) {
        
    }
  }
}
</script>


