<style>
  .card-block{
    padding: 20px;
  }
</style>


<template>
  <div class="col-12">
    <div class="row">
      <div class="col-12">
        <b-card no-block>
          <b-tabs small card @input="tabShown" ref="tabs">
            <b-tab v-for="feature in features" :title="feature.title" v-if="feature.enabled" :key="feature.title">
              <component v-bind:is="feature.component" v-bind:data="data"></component>
            </b-tab>
          </b-tabs>
        </b-card>
      </div>
    </div>
  </div>
</template>

<script>
import { bus } from '../../main';
import GWInfoTab from './tabs/info/gatewayInfoTab.vue';
import GWApiTab from './tabs/api/gatewayApiTab.vue';
import PhysicalGWTab from './tabs/physical/physicalGatewayTab.vue';
import VirtualGWTab from './tabs/virtual/virtualGatewayTab.vue';
import RulesGWTab from './tabs/rules/gatewayRulesTab.vue'

export default {
  props: [
    'data'
  ],
  data: function(){
    return {
      features: {
        info: {
          enabled: false,
          component: GWInfoTab,
          title: 'Info'
        },
        api: {
          enabled: false,
          component: GWApiTab,
          title: 'Api'
        },
        physical: {
          enabled: false,
          component: PhysicalGWTab,
          title: 'Physical'
        },
        virtual: {
          enabled: false,
          component: VirtualGWTab,
          title: 'Virtual'
        },
        rules: {
          enabled: false,
          component: RulesGWTab,
          title: 'Rules'
        }
      },
      defaultFeatures: ['info', 'api', 'virtual'],
      extensionFeatureMap: {
        rules: 'eu.inter-iot.gateway.rules-engine'
      },
      activeTab: null
    }
  },
  created: function() {
    console.log(this.data);
    this.data.gateway.info = {};
    this.data.gateway.api.on(this.data.gateway.api.events.VIRTUALDISCONNECTED, () => {
      this.$emit('openGwComponent', 'gateway-grid', null);
    });
    this.data.gateway.api.on(this.data.gateway.api.events.PHYSICALCONNECTED, err => {
      this.getPhysicalInfo().then(() => {
        this.updateFeatures();
      });
    });
    this.data.gateway.api.on(this.data.gateway.api.events.PHYSICALDISCONNECTED, err => {
      delete this.data.gateway.info.physical;
      this.updateFeatures();
    });
    this.getVirtualInfo().then(() => {
      return this.data.gateway.api.connected.physical ? this.getPhysicalInfo() : new Promise((accept, reject) => {accept();});
    }).then(() => {
      this.updateFeatures();
    });
  },
  beforeDestroy: function() {
    this.data.gateway.api.clear();
    this.data.gateway.api.disconnect();
  },
  methods: {
    getVirtualInfo: function() {
      return this.data.gateway.api.request.virtualInfo()
      .then(resp => new Promise((accept, reject) => {
        this.data.gateway.info.virtual = resp.data;
        accept();
      }));
    },
    getPhysicalInfo: function(){
      return this.data.gateway.api.request.physicalInfo()
      .then(resp => new Promise((accept, reject) => {
        this.data.gateway.info.physical = resp.data;
        accept();
      }));
    },
    updateFeatures: function(){
      if(!this.features.info.enabled)
        this.defaultFeatures.forEach(feature => this.features[feature].enabled = true);
      this.features.physical.enabled = !!this.data.gateway.info.physical;
      let extensions = this.data.gateway.info.virtual.extensions;
      if(this.data.gateway.info.physical) extensions = extensions.concat(this.data.gateway.info.physical.extensions);
      for(let feature in this.extensionFeatureMap) 
        this.features[feature].enabled = extensions.indexOf(this.extensionFeatureMap[feature]) > -1;
      bus.$emit('gwDataUpdate');
    },
    tabShown: function(index){
      const component = this.$refs.tabs.$children[index].$children[0];
      if(this.activeTab && this.activeTab.onHideTab) this.activeTab.onHideTab();
      if(component) this.activeTab = component;
      if(component && component.onShowTab) component.onShowTab();
    }
  }
};
</script>



