<template>
    <!-- Page Content -->
    <div id="page-content-wrapper">
        <div class="container-fluid">
            <div class="row">
                <div class="offset-9 col-3">
                    <b-alert :variant="alertVariant" dismissible @dismiss-count-down="countDownChanged" :show="dismissCountDown">
                        {{ dbResponse }}
                    </b-alert>
                </div>
            </div>
            <div class="row">
                <component v-bind:is="menuitem"></component>
            </div>
        </div>
    </div>
    <!-- /#page-content-wrapper -->
</template>

<script>
import { bus } from '../main';
import GatewayBoard from '../components_app/gateways/gatewayBoard.vue';

export default {
    components: {
        'gateways' : GatewayBoard
    },
    data() {
        return {
            menuitem: 'gateways',
            alertVariant: null,
            dismissCountDown: null,
            dbResponse: null
        }
    },
    created() {
        bus.$on('titleToggle', (data) => {
            this.menuitem = data;
        });
        bus.$on('menuItemSelected', (data) => {
            console.log("main => menuItemSelected: " + data);
            this.dbResponse = null;
            this.menuitem = data;
        });
        bus.$on('dbResponseOk', (data) => {
            console.log("main => dbResponseOk: " + data);
            this.alertVariant = "success";
            this.dismissCountDown = 3;
            this.dbResponse = data;
        });
        bus.$on('dbResponseFail', (data) => {
            console.log("main => dbResponseFail: " + data);
            this.alertVariant = "danger";
            this.dismissCountDown = 5;
            this.dbResponse = data;
        });
        bus.$on('warning', (data) => {
            this.alertVariant = "warning";
            this.dismissCountDown = 5;
            this.dbResponse = data;
        });
    },
    methods: {
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown;
        }
    }
}
</script>



