const Api = function Api($http, baseurl, options) {
  this.$http = $http;
  this.baseurl = baseurl;
  this.options = options;
};

Api.prototype.version = function() {
  return this.$http.get(this.baseurl + '/version', this.options);
}

Api.prototype.virtualInfo = function() {
  return this.$http.get(this.baseurl + '/virtual/info', this.options);
};

Api.prototype.physicalInfo = function() {
  return this.$http.get(this.baseurl + '/physical/info', this.options);
}

Api.prototype.physicalStatus = function() {
  return this.$http.get(this.baseurl + '/physical/status', this.options);
}

Api.prototype.getDevices = function() {
  return this.$http.get(this.baseurl + '/physical/devices', this.options);
}

Api.prototype.deviceStatus = function(id) {
  return this.$http.get(`${this.baseurl}/device/${id}/status`, this.options);
}

Api.prototype.readDevice = function(id) {
  return this.$http.get(`${this.baseurl}/device/${id}/read`, this.options);
}

module.exports = Api;

