import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        user: '',
        userFeatures: [],
        platformSecurityMode: [
            {text: 'Unsecure', value: 'Unsecure'},
            {text: 'Basic+SSL', value: 'Basic+SSL'},
            {text: 'Advanced', value: 'Advanced'}
        ],
        platformTypes: [
            {text: 'FIWARE', value: 'FIWARE'},
            {text: 'universAAL', value: 'universAAL'},
            {text: 'OM2M', value: 'OM2M'},
            {text: 'WSO2', value: 'WSO2'},
            {text: 'INTER-IoT', value: 'inter-iot'},
            {text: 'Sofia2', value: 'Sofia2'},
            {text: 'OpenIoT', value: 'OpenIoT'},
            {text: 'Sensinact', value: 'Sensinact'},
            {text: 'Microsoft Azure', value: 'Microsoft_Azure'},
            {text: 'Amazon AWS IoT', value: 'AWS_IoT'},
            {text: 'BodyCloud', value: 'BodyCloud'},
            {text: 'SEAMS2', value: 'SEAMS2'},
            {text: 'VPA PCS', value: 'VPA_PCS'},
            {text: 'Senscape', value: 'Senscape'},
            {text: 'ITIA CNR Semantic Middleware', value: 'ITIA_CNR'},
            {text: 'E3Tcity', value: 'E3Tcity'}
        ],
        gatewayTypes: [
            {text: 'Physical', value: 'Physical'},
            {text: 'Virtual', value: 'Virtual'}
        ]
    },
    actions: {
        setUserFeatures: function(context, args) {
            // this method calls mutations.setUserFeatures
            context.commit(args.type, args);
        }
    },
    mutations: {
        setUserFeatures: function(state, args) {
            state.user = args.user;
            state.userFeatures = args.userFeatures;
        }
    },
    getters: {
        getUser(state) {
            return state.user;
        },
        getUserFeatures(state) {
            return state.userFeatures;
        },
        getPlatformSecurityMode(state) {
            return state.platformSecurityMode;
        },
        getPlatformTypes(state) {
            return state.platformTypes;
        },
        getGatewayTypes(state) {
            return state.gatewayTypes;
        },
        hasFeature(state) {
            // TODO:
            return function(feature) {
                for (var i = 0; i < state.userFeatures.length; i++) {
                    if (state.userFeatures[i] === feature) {
                        return true;
                    }
                }
                return false;
            };
        }
    },
    modules: {

    }
});



