<template>
  <div class="col-12">
      <div class="row">
        <app-grid v-bind:title="title" v-bind:items="items" v-bind:fields="fields"
          v-bind:actions="actions" v-bind:crudActions="crudActions" v-on:createRegistry="createRegistry()" 
          v-on:editRegistry="editRegistry($event)" v-on:deleteRegistry="deleteRegistry($event)"
          v-on:rowClicked="rowClicked($event)" preventRowClickEdit=true></app-grid>
      </div>
      <div class="row">
        <div class="col-12">
          <b-modal ref="myModalRef" id="modal1" title="Edit Gateway"  size="lg" ok-title="Save" v-on:ok="modalSave();">
            <div class="container-fluid">
              <div class="row">
                <div class="col-6">
                  <span class="small">Name</span>
                  <b-form-input type="text" v-model="selected.name" />
                </div>
              </div>
              <div class="row">
                <div class="col-6">
                  <span class="small">URL</span>
                  <b-form-input type="text" v-model="selected.url" />
                </div>
                <div class="col-6">
                  <span>Port</span>
                  <b-form-input type="text" v-model="selected.port" />
                </div>
              </div>
              <div class="row">
                <div class="col-6">
                  <span class="small">Owner</span>
                  <b-form-input type="text" v-model="selected.owner" />
                </div>
              </div>
            </div>
          </b-modal>
        </div>
    </div>
  </div>
</template>

<script>
import { bus } from '../../main';
import Board from './gatewayBoard.vue';
import Grid from '../../components/grid.vue';
import Api from './api';

export default {
  components: {
    'app-grid': Grid
  },
  data: function(){
    return {
      greeting: 'Hello',
      title: 'Gateways',
      url: '/mongodb_api/gateways',
      crudActions: this.$store.getters.hasFeature('edit_gateways'),
      selected: {},
      items: [],
      fields: {
        name: {
          label: 'Name',
          sortable: true
        },
        url: {
          label: 'URL',
          sortable: true
        },
        port: {
          label: 'Port',
          sortable: true
        },
        owner: {
          label: 'Owner',
          sortable: true
        },
        online: {
          label: 'Status',
          sortable: true,
          formatter: online => online?'<span style="color:green;">Online</span>':online===null?'<span style="color:gray;">Checking...</span>':'<span style="color:red;">Offline</span>'
        },
        version: {
          label: 'Api Version',
          sortable: true,
          formatter: version => version?'<span style="font-weight:bold;">'+version+'</span>':'-'
        },
        modelActions: {
          label: 'Actions',
          create: true,
          edit: true,
          delete: true
        }
      },
      actions: [],
      checkInterval: null
    }
  },
  created: function() {
    this.readRegistries();
    console.log("created!");
    //this.checkInterval = setInterval(this.checkStatus, 5000);
  },
  beforeDestroy: function() {
    clearInterval(this.checkInterval);
  },
  methods: {
    readRegistries: function() {
      this.$http.get(this.url).then( response => {
          this.items = response.data ? response.data.map(item => {
            item.api = new Api(this.$http, item.url + ':' + item.port);
            item.online = null;
            item.version = '?';
            item.api.on(item.api.events.VIRTUALCONNECTED, (err, version) => {
              item.online = !err;
              item.version = err?'-':version;
            });
            item.api.on(item.api.events.VIRTUALDISCONNECTED, (err) => {
              item.online = false;
              item.version = '-';
              item.api.connect();
            });
            item.api.connect();
            return item;
          }) : [];
        },
        this.dbResponseFail
      );
    },
    createRegistry: function() {
        this.selected = {
            name: '',
            type: '',
            url: '',
            port: '',
            owner: '',
            icon: 'sitemap'
        };
        this.$refs.myModalRef.show();
    },
    rowClicked: function(item){
      if(item.online) this.$emit('openGwComponent', 'gateway-tabpane', {gateway: item});
    },
    editRegistry: function(item) {
        this.selected = item;
        this.$refs.myModalRef.show();
    },
    deleteRegistry: function(itemId) {
        this.$http.delete(this.url + '/' + itemId).then(response => {
            this.dbResponseOk(response, "delete");
            this.readRegistries();
        }, response => {
            // error callback
            this.dbResponseFail(response);
        });
    },
    modalSave: function(){
      const item = this.selected;
      if (!item._id) {
        // insert
        this.$http.post(this.url, item).then(response => {
            this.dbResponseOk(response, "insert");
            this.readRegistries();
        }, response => {
            // error callback
            this.dbResponseFail(response);
        });
      } else {
        // update
        this.$http.put(this.url + '/' + item._id, item).then(response => {
            this.dbResponseOk(response, "update");
        }, response => {
            // error callback
            this.dbResponseFail(response);
        });
      }
    },
    dbResponseOk: (response, message) => bus.$emit('dbResponseOk', message),
    dbResponseFail: (response) => bus.$emit('dbResponseFail', response.status + " - " + response.statusText)
  }
};
</script>



