<template>
  <b-modal ref="modal" :title="device.uuid" size="lg" v-on:hide="close();">
    Status: {{device.status}}
    <div slot="modal-footer"></div>
  </b-modal>
</template>
<script>
export default {
  props: [
    'data'
  ],
  data(){
    return {
      listener: null,
      device: {
        status: ''
      }
    }
  },
  methods: {
    close: function(){
      this.data.gateway.api.clear(this.listener);
      this.listener = null;
    },
    open: function(device){
      this.device = device;
      this.$refs.modal.show();
      this.listener = this.data.gateway.api.listen('readDevice', this.device.uuid, (err, data) => {
        console.log(data);
      });
    }
  }
}
</script>


