const ApiWrapper = function ApiWrapper($http, baseurl, timeout, interval){
  this.$http = $http;
  this.baseurl = baseurl + '/api';
  this.httpOptions = {
    timeout: timeout || 3000
  };
  this.interval = interval || 5000;

  this.events = {
    VIRTUALCONNECTED: Symbol('VIRTUALCONNECTED'),
    VIRTUALDISCONNECTED: Symbol('VIRTUALDISCONNECTED'),
    PHYSICALCONNECTED: Symbol('PHYSICALCONNECTED'),
    PHYSICALDISCONNECTED: Symbol('PHYSICALDISCONNECTED')
  };

  this.eventCallbacks = {};

  this.connected = {
    virtual: false,
    physical: false
  };
  this.listeners = {};
};

ApiWrapper.prototype.connect = function(){
  const init = () => {
    this.$http.get(this.baseurl + '/version')
    .then(resp => {
      this._connected(resp.data);
    })
    .catch(resp => {
      this._emit(this.events.VIRTUALCONNECTED, resp);
      setTimeout(init, this.interval);
    });
  };
  init();
};

ApiWrapper.prototype.disconnect = function(){
  this._disconnected();
};

ApiWrapper.prototype._connected = function(version){
  this.version = version;
  const Api = require(`./api_versions/api-v${this.version}`);
  this.request = new Api(this.$http, this.baseurl, this.httpOptions);
  this.updateInterval = setInterval(() => this.updateStatus(), this.interval);
  this.connected.virtual = true;
  this._emit(this.events.VIRTUALCONNECTED, null, version);
  this.updateStatus();
}

ApiWrapper.prototype._disconnected = function(){
  clearInterval(this.updateInterval);
  this.connected.virtual = false;
  this.connected.physical = false;
  this._emit(this.events.VIRTUALDISCONNECTED, null, null);
}

ApiWrapper.prototype.updateStatus = function() {
  this._checkConnection().then(() => {});
};

ApiWrapper.prototype._checkConnection = function(){
  return new Promise((accept, reject) => {
    this.request.version().then(() => {
      this.request.physicalStatus().then(resp => {
        if(resp.data) {
          if(!this.connected.physical) this._emit(this.events.PHYSICALCONNECTED, null, null);
          this.connected.physical = true;
        } else {
          if(this.connected.physical) this._emit(this.events.PHYSICALDISCONNECTED, null, null);
          this.connected.physical = false;
        }
        accept();
      }).catch(resp => {
        if(this.connected.physical) this._emit(this.events.PHYSICALDISCONNECTED, null, null);
        this.connected.physical = false;
        accept();
      });
    }).catch(() => {
      this._disconnected();
      accept();
    });
  });
}

ApiWrapper.prototype._safeCall = function(request, ...params){
  const cb = params[params.length - 1];
  const args = params.slice(0, params.length - 1);
  this.request[request](...args).then(resp => {
    cb(null, resp.data);
  }).catch(resp => {
    this._checkConnection().then(() => {
      cb(resp, null);
    });
  });
}

ApiWrapper.prototype.listen = function(request, ...params){
  if(params.length === 0) throw 'Incorect number of args';
  const s = Symbol('listeners');
  const last = params[params.length - 1];
  const prev = params[params.length - 2];
  const rest = params.slice(0, params.length - 2);
  let interval;
  if(Object.prototype.toString.call(last) == '[object Function]') interval = this.interval;
  else if(Object.prototype.toString.call(prev) == '[object Function]') interval = last;
  else throw 'No callback provided';
  const listener = {
    request: request,
    params: params.slice(0, params.length - 1),
    cb: params[params.length - 1]
  };
  listener.call = () => this._safeCall(listener.request, ...[...listener.params, listener.cb]);
  listener.interval = setInterval(listener.call, interval);
  this.listeners[s] = listener;
  listener.call();
  return s;
};

ApiWrapper.prototype._emit = function(eventSymbol, err, data) {
  if(this.eventCallbacks[eventSymbol])
    for(let s in this.eventCallbacks[eventSymbol])
      this.eventCallbacks[eventSymbol][s](err, data);
};

ApiWrapper.prototype.on = function(eventSymbol, cb) {
  const s = Symbol('eventCallback');
  if(this.eventCallbacks[eventSymbol]) this.eventCallbacks[eventSymbol][s] = cb;
  else this.eventCallbacks[eventSymbol] = {s: cb};
  return s;
};

ApiWrapper.prototype.clear = function(symbol){

  if(!symbol){
    this.eventCallbacks = {};
    Object.getOwnPropertySymbols(this.listeners).map(s => this.listeners[s]).forEach(listener => clearInterval(listener.interval));
    this.eventListeners = {};
    this.listeners = {};
  }

  if(!(typeof symbol === 'symbol')){
    return;
  }
  
  const sname = String(symbol).slice(7, -1) || null;

  switch(sname){
    case 'eventCallback':
      for(let eventType in this.events) if(this.eventCallbacks[this.events[eventType]][symbol]){
        delete this.eventCallbacks[this.events[eventType]][symbol];
        break;
      }
      break;
    case 'listeners':
      if(this.listeners[symbol]){
        clearInterval(this.listeners[symbol].interval);
        delete this.listeners[symbol];
      }
      break;
    default:
      if(this.events[sname]){
        delete this.eventCallbacks[this.events[sname]];
      }
  }
    
}

export default ApiWrapper;


