<style scoped>
  table.info {
    width: 100%;
    height: 400px;
  }
  table.info td {
    padding: 0px;
  }
  table.info td.image {
    width: 150px;
  }
  table.info table.text {
    height: 150px;
    overflow-y: scroll;
    display: block;
    font-size: 14px;
    margin: 0px;
    margin-left: 50px;
    white-space: nowrap;
  }
  table.info table.text tr td {
    border-bottom: 1px solid #b6b6b6;
  }
  table.info table.text tr td:first-child {
    font-weight: bold;
    vertical-align: top;
    padding-right: 35px;
  }
  table.info table.text tr:last-child td {
    border-bottom: none;
  }
</style>
<template>
  <div class="row">
    <div class="col-12">
        <table class="info">
          <tbody>
            <tr>
              <td rowspan="2" class="image">
                <canvas ref="canvas" width="150px" height="400px"></canvas>
              </td>
              <td>
                <table class="text" style="margin-bottom: 50px;">
                  <tbody>
                    <tr v-for="(value, key) in info.virtual" :key="key">
                      <td>{{key.charAt(0).toUpperCase() + key.slice(1)}}</td><td v-html="value"></td>
                    </tr>
                  </tbody>
                </table>
              </td>
              <td style="width: 100%"></td>
            </tr>
            <tr>
              <td style="margin-top: 10px;">
                <table class="text" style="margin-top: 50px;">
                  <tbody>
                    <tr v-for="(value, key) in info.physical" :key="key">
                    <td>{{key.charAt(0).toUpperCase() + key.slice(1)}}</td><td v-html="value"></td>
                    </tr>
                  </tbody>
                </table>
              </td>
              <td style="width: 100%"></td>
            </tr>
          </tbody>
        </table>
    </div>
  </div>
</template>

<script>
import { bus } from '../../../../main';
export default {
  props: [
    'data'
  ],
  data() {
    return {
      info: {
        physical: {},
        virtual: {}
      }
    }
  },
  created: function() {
    bus.$on('gwDataUpdate', () => {
      this.updateInfo();
    });
    this.updateInfo();
  },
  /*beforeCreate: function(){ console.log("beforeCreate"); },
  created: function(){ console.log("created"); },
  beforeMount: function(){ console.log("beforeMount"); },
  beforeUpdate: function(){ console.log("beforeUpdate"); },
  updated: function(){ console.log("updated"); },
  beforeDestroy: function(){ console.log("beforeDestroy"); },
  destroyed: function(){ console.log("destroyed"); },*/
  mounted: function() {
    const canvas = this.$refs.canvas;
    const ctx = canvas.getContext('2d');
    ctx.fillStyle = "rgb(122, 122, 122)";
    ctx.fillRect(73, 75, 4, 250);
    const vgwImg = new Image();
    vgwImg.onload = () => ctx.drawImage(vgwImg, 0, 0, 150, 150);
    vgwImg.src = "/dist/images/server.svg";
    const pgwImg = new Image();
    pgwImg.onload = () => ctx.drawImage(pgwImg, 0, 250, 150, 150);
    pgwImg.src = "/dist/images/rpi.svg";
  },
  methods: {
    updateInfo: function() {
      const v2html = (v) => {
        let html = "";
        if(v instanceof Array) for(let i of v) html+=`${v2html(i)}<br/>`;
        else if(v instanceof Object){
          for(let k in v) {
            if(v.hasOwnProperty(k)) v[k] = v2html(v[k]);
          }
          return v;
        } else html = `<span>${v}</span>`;
        return html;
      };
      ['physical', 'virtual'].forEach(t => {
        if(this.data.gateway.info[t])
          this.info[t] = v2html(this.data.gateway.info[t]);
        else this.info[t] = {};
      });
    },
    gwDataUpdate: function(){
      console.log("data updated!!");
    }
  }
}
</script>


